<template>
    <p class="py-1">{{ texto }}</p>
    <div class="relative">
      <input
        :type="showPassword ? 'text' : 'password'"
        name="password"
        @input="updateInputContent($event.target.value)"
        class="w-full text-sm p-2 px-3 border rounded-lg focus:outline-none focus:border-blue-500"
        :placeholder="placeholder"
        :value="inputValue"
      />
      <button @click.prevent="togglePasswordVisibility" class="absolute top-1/2 right-3 transform -translate-y-1/2 focus:outline-none">
        <i v-if="showPassword" class="fa-solid fa-eye"></i>
        <i v-else class="fa-solid fa-eye-slash"></i>
      </button>
    </div>
  </template>
  
  <script>
  import { ref, watch } from 'vue';
  
  export default {
    props: {
      value: {
        type: String,
        required: true,
      },
      texto: String,
      valor: String,
      placeholder: String,
    },
    setup(props, { emit }) {
      const showPassword = ref(false);
      const inputContent = ref('');
  
      const togglePasswordVisibility = () => {
        showPassword.value = !showPassword.value;
      };
  
      const updateInputContent = (value) => {
        inputContent.value = value;
        emit('update-paswoord', value);
      };
  
      watch(
        () => props.valor,
        (newValor) => {
          inputContent.value = newValor !== '' ? newValor : props.value;
        }
      );
  
      return {
        showPassword,
        inputContent,
        togglePasswordVisibility,
        updateInputContent,
      };
    },
    computed: {
      inputValue() {
        return this.inputContent;
      },
    },
  };
  </script>
  